/* ===============================================================================
Manga CSS
=============================================================================== */
/* mangaBlock01
  ======================================== */

@import "parts/_animation.scss";
@import "parts/_mixin.scss";
@import "parts/_swiper.scss";

.mangaBlock01 {
  min-width: 984px;
  background: #ebd3e1;
  padding: 39px 0 70px;
  margin-top: -5px;
  @include PC {margin-bottom: 34px;}
  @include SP {margin-bottom: 16px;}
  > .inner {
    width: 984px;
    margin: 0 auto;
    text-align: center;
  }
}
.backnumber{
  font-weight: bold;
  letter-spacing: .1em;
  @include PC {margin-top: 90px;}
  @include SP {margin-top: 30px;}
  h2{
    @include PC {font-size: 19px;}
    @include SP {font-size: 10px;}
    margin-bottom: 2px;
  }
  .txt{
    @include PC {font-size: 28px;}
    @include SP {font-size: 13px;}
  }
  .img{
    @include PC {margin-top: 24px;}
    @include SP {
      width: 70.3%;
      min-width: 250px;
      margin: auto;
      margin-top: 10px;
    }
  }
  .btn{
    @include PC {margin-top: 30px;}
    @include SP {
      margin-top: 10px;
      text-align: center;
      img{
        width: 39.06%;
        min-width: 125px;
      }
    }
    a {
      @include PC {width: 220px;}
      @include SP {
        width: 39%;
        min-width: 125px;
      }
      &:hover{
        opacity: .7;
        transition-property: opacity;
        transition-duration: .2s;
      }
    }  
  }
}
@media screen and (max-width: 768px) {
  .mangaBlock01 {
    min-width: 0;
    min-width: initial;
    padding: 20px 0 30px;
    margin-top: 0;
    > .inner {
      width: auto;
    }
    .heading {
      p:first-child {
        width: 168px;
        margin: 0 auto 7px;
      }
      h2 {
        font-size: 2.034rem;
        margin-bottom: 8px;
      }
      p:last-child {
        font-size: 0.833rem;
      }
    }
  }
}