.fade-up {
  transition: all 1500ms ease;
  opacity: 0;
  transform: translate(0, 150px);
  &.on{
    opacity: 1;
    transform: translate(0, 0px);
  }
}

.fade-in {
  transition: all 1000ms ease;
  opacity: 0;
  &.on{opacity: 1;}
}
.fade-inLoad{
  transition: all 1500ms ease;
  opacity: 0;
  &.on{opacity: 1;}
}
$transition: 500ms;
.delay1{
  transition-delay: $transition !important;
}
.delay2{
  transition-delay: $transition*1.5 !important;
}
.delay3{
  transition-delay: $transition*2 !important;
}
.delay4{
  transition-delay: $transition*2.5 !important;
}
.delay5{
  transition-delay: $transition*3 !important;
}
.delay6{
  transition-delay: $transition*3.5 !important;
}
.delay7{
  transition-delay: $transition*4 !important;
}
.delay8{
  transition-delay: $transition*4.5 !important;
}
.delay9{
  transition-delay: $transition*5 !important;
}